    color: white;
}

.contents{
    padding: 155px 120px;
    display: flex;
}

.conbuttons button{
    background-color: cornflowerblue;
    color: white;
    border: none;
    padding: 9px;
    border-radius: 77px;
    font-size: 13px;
}

.conimage{
    margin: auto;
}

.conimage img{
    width: 16vw;
    border-radius: 10px;
    margin-bottom: 46px;
    margin-left: 92px;
}

