    padding: 100px;
}

.p-3{
    font-family: "Open Sans", sans-serif;
    color: #444444
}

.p-3 h5{
    color: #37517e;
    font-weight: 500;
    font-size: 1.5rem;
}

.col{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin: 0px 12px;
    padding: 8px;
    background-color: white;
    color: black;
    /* animation-timing-function: ease; */
    transition: 1s;
}

.col:hover{
    transform: translateY(-13px);
}

