@media (max-width:460px){

    .contents {
        padding: 155px 40px;
    }

    .textcontent h1{
        font-size: 15px;
    }

    .textcontent p{
        font-size: 10px;
    }

    .conbuttons button{
        padding: 6px;
        border-radius: 9px;
        font-size: 7px;
    }
}