.form-title{
    text-align: center;
}

.register-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.signupformgroup{
    margin: 5px 0px;
    border-bottom: 1px solid;
    margin-bottom: 40px;
    /* width: 25%; */
}
.signupformgroup input{
    border: none;
    background: transparent;
    margin-left: 15px;
    margin-bottom: 06px;
}

.signupformgroup input:focus{
    outline: none;
}

.signup-image{
    text-align: center;
    margin: 25px 0px;
}

@media (max-width:645px){
    .col{
        margin-bottom: 30px;
    }
}

@media(max-width:575px){
    .p-3 h5{
        font-size: 14px;
    }

    .p-3 p{
        font-size: 10px;
    }
}

@media (max-width:503px){
    .p-3 h5 {
        font-size: 12px;
    }

    .p-3 p {
        font-size: 8px;
    }
}

@media(max-width:493px){
    .p-3 h5{
        font-size: 10px;
    }

    .p-3 p{
        font-size: 7px;
    }

    .scont{
        padding: 24px;
    }
}

@media (max-width:466px){
    .scont{
        padding: 6px;
    }
}

@media (max-width:430px){
    .scard{
        padding: 76px;
    }
}

@media (max-width:345px){
    .scont{
        padding: 0px;
    }
}

