@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Bree+Serif&family=Cormorant+Garamond:wght@300&family=Mouse+Memoirs&family=Mukta:wght@300&family=Noto+Sans:wght@500&family=Roboto+Mono&family=Roboto+Serif:opsz,wght@8..144,100&family=Rubik+Iso&family=Rubik:ital,wght@0,400;0,700;1,500&family=Ubuntu:wght@300&display=swap');

.contact_form_title{
    /* text-align: center; */
    margin: 22px 0px;
    font-family: 'Noto Sans', sans-serif;
    font-weight: bold;
}

.contact_form_name input{
    border: none;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 8px;
    margin: 22px 0px;
    margin-right: 40px;
}

.contact_form_text textarea{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border: none;
    padding: 6px;
    margin: 20px 0px;
    width: -webkit-fill-available;
}

.contact_form_button button{
    border: none;
    padding: 7px;
    border-radius: 11px;
    background-color: rgb(27, 68, 142);
    color: white;
    /* width: 100px; */
}

@media (max-width:777px){
.contact_form_name input{
    padding: 0px;
    font-size: 11px;
}
}

@media (max-width:524px){
    .contact_form_name input{
        font-size: 9px;
        margin-right: 15px;
        width: 90px;
    }
}

@media (max-width:400px){
    .contact_form_name input {
        font-size: 7px;
        margin-right: 9px;
        padding: 3px;
    }    
}

@media (max-width:295px){
    .contact_form_name input {
        font-size: 5px;
        margin-right: 5px;
        padding: 3px;
    }
}