@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Bree+Serif&family=Cormorant+Garamond:wght@300&family=Mouse+Memoirs&family=Mukta:wght@300&family=Noto+Sans:wght@500&family=Roboto+Mono&family=Roboto+Serif:opsz,wght@8..144,100&family=Rubik+Iso&family=Rubik:ital,wght@0,400;0,700;1,500&family=Ubuntu:wght@300&display=swap');

.sameback {
    background-color: rgb(27, 68, 142);
    font-family: 'Bree Serif', serif;
}

.navbar-collapse {
    justify-content: end;
}

.collapse {
    justify-content: center;
    display: flex;
}

/* .navbar-nav li{
    margin-right: 25px;
} */

.nav-item {
    margin: 0px 12px;
}

.nav-link {
    color: white;
}

.btns {
    padding: 7px;
    border-radius: 4px;
}

.hamburger {
    display: none;
    cursor: pointer;
    margin: 10px;
        position: absolute;
        top: 0;
        right: 0px;
}

