.imgm{
    background-image: url('a1.jpg');
    background-size: cover;
    /* height: 700px; */
    background-repeat: no-repeat;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: end;
    padding: 73px;
}

.redcont{
    background-color: rgb(27, 68, 142);;
    color: white;
    width: fit-content;
    margin-bottom: 20px;
}

.redcont h4{
    padding: 16px;
    text-align: center;
}

.rbutton button{
    background-color: transparent;
    border: none;
    padding: 10px;
    color: rgb(27, 68, 142);;
    border: 1px solid;
    font-family: cursive;
}

.rbutton button:hover{
    background-color: rgb(27, 68, 142);
    color: white;
}

.introcont{
    margin: auto;
    margin-top: 250px;
}

.mrg{
    margin-bottom: 20px;
}

.colorsame{
    color: rgb(27, 68, 142);
}

.link{
    text-decoration: none;
    color: unset;
    /* display: flex; */
}

.link p{
    margin-right: 10px;
    margin-bottom: 0px;
}

.emailsec button{
    background-color: rgb(27, 68, 142);
    border: none;
    padding: 10px;
    color: white;
    font-family: cursive;
    display: flex;
}

.docsec button{
    background-color: rgb(27, 68, 142);
    border: none;
    padding: 10px;
    color: white;
    font-family: cursive;
    display: flex;
}

@media (max-width:760px){
    .imgm{
        background-size: 100% 96%;
        padding: 18px;
    }

    .redcont h4{
        font-size: 15px;
    }

    .introcont {
        margin-top: 100px;
    }

    .rbutton{
        font-size: 10px;
    }
}

@media (max-width:512px){
    .redcont h4{
        font-size: 9px;
        padding: 6px;
    }
}

@media(max-width:520px){
    .flex{
        font-size: 14px;
    }

    .mrg{
        margin-bottom: 11px;
    }
}